import React, {Component} from 'react';

export default class Stats extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    componentDidMount = () => {
        let {data} = this.props;

        if(data) {
            // pull out the headers for the table
            let headers = this.extractHeaders(data);

            // pull out the rows from the table
            let table = this.extractRows(data, headers);

            let tableJSX = this.renderData(table, headers);

            // set the state of the headers and the data
            this.setState({
                table: table, 
                headers: headers, 
                tableJSX: tableJSX
            });
        }
    }

    extractHeaders = (data) => {
        let keys = Object.keys(data);
        return keys;
    }

    extractRows = (data, headers) => {

        // check to make sure headers exist
        if(headers && headers.length > 0) {

            // get the keys/id's of all the rows
            let keys = Object.keys(data[headers[0]]);

            // set the length of all the rows
            let length = keys.length;

            // initialize the table array
            let table = [];

            // loop through the rows
            for(let i=0; i<length; i++) {

                // initialize a temporary array variable for the row
                let row = [];

                // loop through the headers
                for(let head of headers) {

                    // push the value of the row based on the header
                    row.push(data[head][i]);
                }

                // push the row to the table
                table.push(row);
            }
            
            // return the table
            return table;
        }
    }

    renderData = (table, headers) => {
        return (
            <table>
                <thead>
                    <tr>
                        {headers.map(head => (
                            <th key={head}>{head}</th>
                        ))}
                    </tr>
                </thead>
                <tbody>
                    {table.map(row => (
                        <tr>
                            {row.map(item => (
                                <td>{item}</td>
                            ))}
                        </tr>
                    ))}
                </tbody>
            </table>
        );
    }

    render() {
        let {tableJSX} = this.state;
        return (
            <div className="stats">
                {tableJSX}
            </div>
        );
    }
}