import React, {Component} from 'react';

export default class Header extends Component {
    render() {
        let {changeDisplay} = this.props;
        return(
            <div className="header" >
                <div className="header-logo-wrapper" >
                    <img className="header-logo" src={require('../../images/logo2.png')} alt="logo"/>
                </div>
                <div className="header-news" onClick={()=>changeDisplay('news')}>
                    <span className="header-link">News</span>
                </div>
                <div className="header-about" onClick={()=>changeDisplay('about')}>
                    <span className="header-link">About</span>
                </div>
                <div className="header-data" onClick={()=>changeDisplay('stats')}>
                    <span className="header-link">Data</span>
                </div>
            </div>
        )
    }
}