import React, {Component} from 'react';
import Header from './header/Header';
import About from './body/About';
import NewsFeed from './body/NewsFeed';
import Stats from './body/Stats';

export default class App extends Component {
    constructor() {
        super();
        this.state = {display: 'stats'};
    }

    componentDidMount = () => {
        // pull the data from the json file
        let data = require('../data/nba_stats.json');

        this.setState({data: data});
    }

    changeDisplay = (value) => {
        console.log('display: ', value);
        this.setState({display: value});
    }

    render() {
        let {display, data} = this.state;
        return (
            <div className="body" onTouchStart={(e)=>e.preventDefault()}>
                <Header changeDisplay={this.changeDisplay}/>
                {display === 'about' ?
                    <About />
                : display === 'stats' ?
                    data ? <Stats data={data}/> : null
                : 
                    <NewsFeed />
                }
            </div>
        );
    }
}